import http from '../assets/js/http.js'
export default {
  /**
     * 获取当今的最新行情
     */
  getAllLatestPrices: () => {
    return http.thirdPartyGet('api/tick/getAllLatestPrices', {}).then((res) => {
      return Promise.resolve(res.Result);
    });
  },
  /* 获取币种行情详情 (页面头部信息) */
  getLatestMarketDetail: (coin) => {
    return http.thirdPartyGet('api/tick/getLatestMarketDetail', {market: coin})
      .then((res) => {
        return Promise.resolve(res.Result);
      });
  },

  /* 获取币种行情的K线图 */
  getLatestKlinesDetail: (coin, time) => {
    return http.thirdPartyGet('api/kline/getKlines', { market: coin, period: time })
      .then((res) => {
        return Promise.resolve(res.Result);
      });
  },

  
}



