import http from '../assets/js/http.js'
import URLUtil from "../util/URLUtil"
import SysUtil from "../util/SysUtil"
export default {
  /**
   * 是否可以进行认证
   */
  isUseAuth: () => {
    let type = URLUtil.getValue("type");
    let access_token = URLUtil.getValue("access_token");
    if (access_token && type == "authorization") {
      return true;
    } else {
      return false;
    }
  },
  /**
   * 用户对应用进行授权
   */
  authorize: () => {
    let access_token = URLUtil.getValue("access_token");
    return http.post("/oauth2/authorization", {
      access_token: access_token
    });
  },
  /**
   * 授权结果通知
   */
  authNotice: (res) => {
    let resStr = JSON.stringify(res);

    setTimeout(()=>{
        // 当系统为unity授权
    if(URLUtil.getValue("sys")=="unity"){
      window.location.href = "uniwebview://mofangvr.com?action=authNotice&res="+resStr;
    }
    if (SysUtil.isAndroid()) {
      // android
      window.exchange.onAuthorizate(resStr);
      return;
    }
    if (SysUtil.isIos()) {
      window.webkit.messageHandlers.onAuthorizateByexchange.postMessage(resStr);
      return;
    }
    },2000);
  }
}



