/**
 *  处理接口的事务
 */

import modal from './modal'
import axios from "axios";

class Api{
    constructor(){
        let _url = "" , env = process.env.NODE_ENV;
        console.log(env)
        switch(env){
           case  "develop" : _url = "//develop.mofangvr.com";break;
           case  "test" : _url = "//test.mofangvr.com";break; 
           default :  _url = "";
        } 
        this.serverUrl = _url;
        this.instance = axios.create({
            baseURL: _url,
            timeout: 5000,
        })

        //拦截请求
        this.instance.interceptors.request.use(config => {
                  return config
        },error => {
            Promise.reject('请求出错！')   
        }) 

        //拦截响应
        this.instance.interceptors.response.use(
            response => {
              const res = response.data;
              if (res.code === 0) {
                   return res;
              }
            },
            error => {
              let errorResponse = error.response
              modal.showToast(errorResponse.data.message)
              return Promise.reject(errorResponse.data)
            }
          )
     } 

       /**
     * 请求json格式数据, get
     * @param {string} url 
     * @param {object} data 
     * @param {function} suc 
     * @param {function} errA
     */
     getJson(url,data,suc,errA){
         this.instance.get(url,{params : data})
         .then(
             res => {
                  suc(res)
                }
        ).catch(
            err => {
                errA(err)
               }
        )
     }

     /**
     * 请求json格式数据, post
     * @param {string} url 
     * @param {object} data 
     * @param {function} suc 
     * @param {function} errA
     */

    postJson(url,data,suc,errA){
        this.instance.post(url,data)
        .then(
            res => {
                 suc(res)
               }
       ).catch(
           err => {
               errA(err)
                  console.log(err,'err') 
              }
       )
    }

    /**
     * 请求json格式数据, get
     * @param {string} url 
     * @param {object} data 
     * @param {function} suc 
     * @param {function} errA
     */
  /*   getJson(url,data,suc,errA,option){
        let self = this;
        $.ajax({
             type : "get",
             url : self.serverUrl + url,
             data : data,
             headers : option && option.headers || null,
             dataType : "json",
             beforeSend : option && option.beforeSend,
             success : function(res){
                   suc(res);
             },
             error : function(err){
                   if(err.status >= 400 &&  err.status < 500){
                        modal.showToast(JSON.parse(err.response).message);
                   }else if(err.status >= 500){
                   }
                   errA && errA(err);
                   console.log("接口请求有误!");
            }
      })
   } */

   /**
     * 请求json格式数据, post
     * @param {string} url 
     * @param {object} data 
     * @param {function} suc 
     * @param {function} errA
     */
    /* postJson(url,data,suc,errA,option){
        let self = this;
        $.ajax({
             type : "post",
             url :  self.serverUrl + url,
             data : data,
             headers : option && option.headers || null,
             beforeSend : option && option.beforeSend,
             success : function(res){
                  suc(res);
             },
             error : function(err){
                  
                  if(err.status >= 400  &&  err.status < 500){
                        modal.showToast(JSON.parse(err.response).message);
                    }else if(err.status >= 500){
                         modal.showToast("服务端异常!");
                    }
                    errA && errA(err);
                    console.log("接口请求有误!");
             }
      })
   } */


     /**
      * 模拟请求，开发常用
      * 
      */ 
     
     copyRequest(callback,time){
          setTimeout(callback,time || 100);
     }
}



export default new Api();





