import http from '../assets/js/http.js'
export default {
  getInfo: (accessToken) => {
    return http.post('exchange/getAppInfo', {
      access_token: accessToken
    });
  },
  /**
   * 获取mcc 商品列表
   */
  getMCCGoodList: () => {
    return http.get('MCCGood/get', {});
  },
  /**
   * 微信支付
   * @param {*} product_id
   */
  wxPay: (productId) => {
    return http.post('pay/wxh5', {
      product_id: productId
    }).then((res) => {
      return Promise.resolve(res.result.res);
    });
  }
}



