import request from "../assets/js/common.js";
import util from "./../assets/Tools/util.js";
import Api from "../assets/Tools/Api.js";
import cookie from "common-cookie";
import renderDay from "../assets/js/day.js";
import renderDay2 from "../assets/js/day2.js";
import renderTime from "../assets/js/time.js";
import eventBus from "../assets/js/eventBus.js";
import market from "./../model/market.js";
import marketDetail from "./../model/marketDetail.js";
import headerView from "./headerBack";

const url = "http://106.14.46.186";
const testUrl = "https://leyou.586886.com/";
const timeMap = {
  0: 1,
  1: 1440,
  2: 10080
};
const KlineTime = {
  0: "1min",
  1: "1day",
  2: "1week"
};

let inter = 0;

export default {
  components: {
    headerView
  },
  data() {
    return {
      coinsType: "", //币种类型
      isRised: false, //是否上升
      curNav: 0,
      CryptoAddr: 5, //交易所用户ID
      teamIndex: 0,
      tradeIds: [], //交易所所有的uids
      tradeId: "", //在交易所的id
      uid: "", //用户uid
      balances: [], //所有用户在交易所的财产情况
      orderId: 0, //交易ID,
      userAccessToken: "",
      limtPramas: {}, //下买卖单参数
      topData: {
        curPrice: 0, //当前价格
        heighest: 0, //最高价
        lowest: 0, //最低价
        buy: 0, //买入价
        sell: 0, //卖出价
        profit: 0, //盈利比例
        volume: 0, //成交量
        title: "",

        new: 0, //需要的数据 最新
        market: 0, //需要的数据 市价
        average: 0, //需要的数据 均价
        quantum: 0, //需要的数据 额限
        quantity: 0, //需要的数据 数量
        yesterday: 0, //需要的数据 昨天价格
        profitImage: "" //需要的数据 涨幅图片
      },
      newRecords: {
        //当前用户的委托中的订单集
        buy: [],
        sell: []
      },

      finishedRecords: {
        //已经成交的订单集
        buy: [],
        sell: []
      },

      wealth: {
        //个人财富
        ZSB: {
          //钻石
          value: 0,
          pending: 0
        },
        MFS: {
          //魔法石
          value: 0,
          pending: 0
        }
      },

      showBuy: false,
      //底部数据模版
      btmTemp: {
        Price: "价格",
        Num: "数量"
      },
      lowlimit: 0,
      heighlimit: 100,
      availableMount: 100,
      isBuy: true,
      token: "",
      orders: [], //我的订单
      message: {
        title: "提示",
        content: ""
      },
      showModel: false,
      detailView: false,
      openOrdersListL: [] //未戳和成功得订单列表
    };
  },
  methods: {
    getCoinType: function() {
      eventBus.$on("getMarketType", message => {
        console.log(message);
        this.coinsType = message.replace("_", "/");
      });
    },
    alert(message) {
      this.showModel = true;
      this.message.content = message;
    },

    back() {
      this.$router.go(-1); //返回上一层
    },

    confirm() {
      this.showModel = false;
      this.message.content = "";
    },
    //减少
    plus(type) {
      this.btmTemp.Price = Number.parseInt(this.btmTemp.Price, 10);
      this.btmTemp.Num = Number.parseInt(this.btmTemp.Num, 10);
      //价格
      if (type === 0) {
        if (isNaN(this.btmTemp.Price)) {
          this.btmTemp.Price = 0;
        }
        this.btmTemp.Price > 0 && (this.btmTemp.Price -= 1);
      } else if (type === 1) {
        //数量
        if (isNaN(this.btmTemp.Num)) {
          this.btmTemp.Num = 0;
        }
        this.btmTemp.Num > 0 && (this.btmTemp.Num -= 1);
      }
    },
    //增加
    add(type) {
      this.btmTemp.Price = Number.parseInt(this.btmTemp.Price, 10);
      this.btmTemp.Num = Number.parseInt(this.btmTemp.Num, 10);
      //价格
      if (type === 0) {
        if (isNaN(this.btmTemp.Price)) {
          this.btmTemp.Price = 0;
        }
        this.btmTemp.Price += 1;
      } else if (type === 1) {
        //数量
        if (isNaN(this.btmTemp.Num)) {
          this.btmTemp.Num = 0;
        }
        this.btmTemp.Num += 1;
      }
    },
    //分时／日k/七日
    selectNav(index) {
      if (this.curNav === index) return;
      this.curNav = index;
      this.mainRequest();
      if (this.curNav !== 0) {
        clearInterval(inter);
        console.log(inter, "inter");
      } else {
        inter = setInterval(() => {
          this.mainRequest();
        }, 1000 * 10);
      }
    },

    //缩回购买栏
    close() {
      this.showBuy = false;
    },

    //委托订单或者最新成交
    selectTeam(index) {
      this.teamIndex = index;
    },

    //点击买
    buy() {
      this.showBuy = true;
      this.isBuy = true;
    },

    //点击卖
    sell() {
      this.showBuy = true;
      this.isBuy = false;
    },

    /**
     *  买卖订单提交
     */
    submitOrder() {
      let type = 0;
      if (this.isBuy) type = 1;
      else type = 2;

      if (
        isNaN(this.btmTemp.Price) ||
        isNaN(this.btmTemp.Num) ||
        !this.btmTemp.Price ||
        !this.btmTemp.Num
      ) {
        this.$vux.toast.text("请填写正确格式的价格和数量");
        return;
      }
      this.limtPramas = {
        market: this.coinsType,
        price: this.btmTemp.Price,
        quantity: this.btmTemp.Num
      };
      if (type == 1) {
        marketDetail
          .BuyLimit(this.limtPramas)
          .then(res => {
            if (res.success) {
              this.$vux.toast.text("交易成功");
              this.close();
              this.getOpenOrderList();
            } else {
              this.$vux.toast.text(res);
            }
          })
          .catch(res => {
            this.$vux.toast.text(res);
          });
      } else {
        marketDetail
          .SellLimit(this.limtPramas)
          .then(res => {
            if (res.success) {
              this.$vux.toast.text("交易成功");
              this.close();
              this.getOpenOrderList();
            } else {
              this.$vux.toast.text(res);
            }
          })
          .catch(res => {
            this.$vux.toast.text(res);
          });
      }
    },

    /**
     *     向交易所告知更新某个用户的货币情况
     *     @param {object} data, uid,name,available
     *     @param {Function} callback
     * */
    uploadCoins(data, callback) {
      let postParam = {
        SiteName: this.type,
        Accounts: [
          {
            SiteUser: data.uid,
            Balances: [
              {
                Coin: data.name,
                Available: data.available
                /*  Pending : data.pending */
              }
            ]
          }
        ]
      };

      postParam = JSON.stringify(postParam);

      request(
        url + "/api/third/SetBalances",
        "post",
        postParam,
        res => {
          callback && callback(res);
        },
        err => {
          console.log(err, "err");
        }
      );
    },

    /**
     * 向交易所查询获取某个用户的货币情况并更新到数据库
     * @param {object} data
     * */
    upDateCoins(data, callback) {
      let postParam = {
        SiteName: this.type,
        Accounts: data.accounts
      };

      postParam = JSON.stringify(postParam);

      request(
        url + "/api/third/GetBalances",
        "post",
        postParam,
        res => {
          callback && callback(res);

          let diamond = {},
            wealth = {};

          res.Result[0].Balances.map(item => {
            if (item.coin === "ZSB") {
              diamond.diamond = item.Available;
              diamond.pending = item.Pending;
            }
            if (item.coin === "MFS") {
              wealth.wealth = item.Available;
              wealth.pending = item.Pending;
            }
          });

          let _data = {
            uid: this.uid,
            diamond: diamond.diamond,
            wealth: wealth.wealth
          };

          /*                             this.upDatePerson(_data) */
        },
        err => {
          console.log(err, "err");
        }
      );
    },

    // 撤销订单
    withdrawOrder(order, callback) {
      let postParam = {
        CryptoAddr: this.tradeId,
        OrderID: order.id
      };

      postParam = JSON.stringify(postParam);

      request(
        url + "/api/third/CancelOrder",
        "post",
        postParam,
        res => {
          if (res.Success) {
            this.alert("撤单成功");

            let _orders = this.orders.filter(item => {
              if (item.id === order.id) {
                return;
              }
              return item;
            });

            this.orders = _orders;

            callback && callback();
          } else {
            this.alert(res.ErrorMsg);
          }
        },
        err => {
          console.log(err, "err");
        }
      );
    },

    //获取该用户所有交易数据
    getTradeData(callback) {
      let postParam = {
        CryptoAddr: 3
      };

      postParam = JSON.stringify(postParam);

      request(
        url + "/api/third/OrderList",
        "post",
        postParam,
        res => {
          if (res.Success) {
            callback && callback();
          } else {
            this.alert(res.ErrorMsg);
          }
        },
        err => {
          console.log(err, "err");
        }
      );
    },

    //K线买入卖出最新数据
    getKNewData(callback) {
      request(
        url + "/api/kline/getKlines",
        "post",
        null,
        res => {
          if (res.Success) {
            callback && callback(res.Result);
          } else {
            this.alert(res.ErrorMsg);
          }
        },
        err => {
          console.log(err, "err");
        }
      );
    },

    //获取K线图数据
    getKAllData(time, callback) {
      let postParam = {
        time: time //时间段数据
      };
      console.log(postParam, "time");

      postParam = JSON.stringify(postParam);

      let curNavType = KlineTime[this.curNav];
      $.ajax({
        type: "get",
        url:
          testUrl +
          "/api/kline/getKlines?market=" +
          this.coinsType +
          "&period=" +
          curNavType,
        success: function(data) {
          // console.log(data.Result);
          let kLineDate = data.Result;
          let terData = {};
          let dates = [];
          let Kdata = [];
          let volumes = [];
          let kTime;
          kLineDate.forEach(item => {
            let _time = new Date(item.time * 1000);
            let otherTime = new Date(item.time);
            if (curNavType === "1min")
              dates.push(
                util.addSingleTime(_time.getHours()) +
                  ":" +
                  util.addSingleTime(_time.getMinutes()) +
                  ":" +
                  util.addSingleTime(_time.getSeconds())
              );
            else dates.push(getMyDate(otherTime));
            let _data = [];
            _data.push(item.open);
            _data.push(item.close);
            _data.push(item.high);
            _data.push(item.low);
            Kdata.push(_data);
            // dates.push(_time)
            volumes.push(item.volume);
          });

          terData.data = {
            dates: dates,
            data: Kdata,
            volumes: volumes
          };
          // console.log(terData);

          let app = echarts.init(document.querySelector("#market"));
          let option = renderTime(terData);
          app.setOption(option);
        }
      });

      function getMyDate(str) {
        var oDate = new Date(str),
          oYear = oDate.getFullYear(),
          oMonth = oDate.getMonth() + 1,
          oDay = oDate.getDate(),
          oHour = oDate.getHours(),
          oMin = oDate.getMinutes(),
          oSen = oDate.getSeconds(),
          oTime = oYear + "-" + getzf(oMonth) + "-" + getzf(oDay);
        return oTime;
      }
      //补0操作
      function getzf(num) {
        if (parseInt(num) < 10) {
          num = "0" + num;
        }
        return num;
      }
    },

    //请求且变化数据
    mainRequest() {
      //币种详细信息
      market.getLatestMarketDetail(this.coinsType).then(res => {
        let rs = res;

        this.topData = {
          curPrice: rs.price, //当前价格
          //最高价
          heighest:
            rs.high24H > 10000
              ? util.converToW(rs.high24H)
              : rs.high24H.toFixed(2),
          //最低价
          lowest:
            rs.low24H > 10000
              ? util.converToW(rs.low24H)
              : rs.low24H.toFixed(2),
          buy: util.converToW(rs.buy_price), //买入价
          mall: util.converToW(rs.sell_price), //卖出价
          profit: rs.changePercentage, //盈利比例
          volume: util.converToW(rs.volume24H), //成交量
          title: rs.market, //币种名字

          new: 2.01, //需要的数据 最新
          market: 1.98, //需要的数据 市价
          average: 1.98, //需要的数据 均价
          quantum: 12548, //需要的数据 额限
          quantity: 20125, //需要的数据 数量
          yesterday: 0.18, //需要的数据 昨天价格
          profitImage: "" //需要的数据 涨幅图片
        };
      });

      //币种的详细K线图 K线不会弄，暂时弄到这。
      market.getLatestKlinesDetail(this.coinsType, "1min").then(res => {
        // console.log("res_getLatestKlinesDetail", res);
        let kLineDate = res;
        let terData = {};
        let dates = [];
        let Kdata = [];
        let volumes = [];
        let kTime;
        let curNavType = "1min";

        kLineDate.forEach(item => {
          let _time = new Date(item.time * 1000);
          let otherTime = new Date(item.time);
          if (curNavType === "1min")
            dates.push(
              util.addSingleTime(_time.getHours()) +
                ":" +
                util.addSingleTime(_time.getMinutes()) +
                ":" +
                util.addSingleTime(_time.getSeconds())
            );
          else dates.push(getMyDate(otherTime));

          let _data = [];

          _data.push(item.open);
          _data.push(item.close);
          _data.push(item.high);
          _data.push(item.low);

          Kdata.push(_data);

          volumes.push(item.volume);
        });

        terData.data = {
          dates: dates,
          data: Kdata,
          volumes: volumes
        };

        let app = echarts.init(document.querySelector("#market"));
        let option = renderTime(terData);
        app.setOption(option);
      });
    },

    //获取未戳和的订单列表
    getOpenOrderList() {
      marketDetail
        .getOpenOrdersDetail(this.coinsType)
        .then(res => {
          console.log("OpenOrdersDetail", res);
          if (res != "") {
            this.newRecords = {
              buy: [],
              sell: []
            };
            console.log("未戳和订单", res);
            res.forEach((item, index) => {
              if (item.side == 0) {
                this.newRecords.buy.push({
                  price: util.converToW(item.price),
                  amount: util.converToW(item.quantity)
                });
              } else {
                this.newRecords.sell.push({
                  price: util.converToW(item.price),
                  amount: util.converToW(item.quantity)
                });
              }
            });
          }
        })
        .catch(function(e) {
          console.log("error", e);
        });
    },

    //获取我的订单
    getUerOrderList() {
      marketDetail.userOrderList(this.coinsType).then(res => {
        res.forEach((item, index) => {
          console.log(item);
          this.orders.push({
            side: item.side,
            num: item.volume,
            isCancelled: item.isCancelled,
            market: item.market,
            price: item.price,
            volume: item.quoteVolume
          });
        });
      });
    }
  },
  created() {},
  activated() {
    console.log("传入代码", this.$route.params.coin);
    let coin = this.$route.params.coin;
    this.coinsType = coin.replace("_", "/");
    this.getCoinType();

    this.getOpenOrderList();
    this.getUerOrderList();

    //启动
    this.mainRequest();
  }
};



