import Footer from "./components/footer";
import reset from "./assets/css/reset.css";
import user from "./model/user.js";
require("./assets/css/style.css");
require("./assets/css/animal.css");
import URLUtil from "./util/URLUtil.js";

export default {
  components: {
    Footer
  },
  name: "App",
  data() {
    return {
      routeStack: [],
      enterAnimate: "", //页面进入动效
      leaveAnimate: "", //页面离开动效
      marketDetailView: false
    };
  },
  watch: {
    $route(to, from) {
      if (/app/.test(to.path) && /app/.test(from.path)) {
        console.log("what?");
        this.enterAnimate = "";
        this.leaveAnimate = "";
        return;
      }
      to.meta.keepAlive = false; // C 跳转到 A 时让 A 不缓存，即刷新

      let routeFrom = this.routeStack.pop();

      if (typeof routeFrom == "undefined" || routeFrom != to.path) {
        this.routeStack.push(from.path);
      }

      this.enterAnimate =
        routeFrom == to.path ? "animated fadeInLeft" : "animated fadeInRight";

      this.leaveAnimate =
        routeFrom == to.path ? "animated fadeOutRight" : "animated fadeOutLeft";
    }
  },
  methods: {
    to() {
      this.marketDetailView = true;
    }
  },
  created() {
    if (
      URLUtil.getValue("type") == "authorization" &&
      URLUtil.getValue("sys") == "unity"
    )
      this.$router.push("/authorization");
  }
};



