export default {
  name: "headerBack",
  props: {
    title: {
      type: String,
      default: "标题"
    },
    isBack: {
      type: null,
      default: 1
    }
  },
  data() {
    return {
      Back:true
    };
  },
  mounted() {
     if(this.isBack==0){
       this.Back = false;
     }
  }
};



