import headerView from "../../components/headerBack";
import newsView from "./newsView";
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import news from "./../../model/news.js";
import EventBus from './../../assets/js/eventBus.js';

export default {
    components: {
        headerView,
        newsView,
        swiper,
        swiperSlide
    },

    data() {
      return {
        title:  this.$t("message.tapNews"),
        swiperOption: {//以下配置不懂的，可以去swiper官网看api，链接http://www.swiper.com.cn/api/
          speed:500,
          autoplay:true,
          pagination:{
            el: '.swiper-pagination',
            clickable:true,
          }, 
          autoHeight: true,
        },
        banner:[],
        array:[],
      }
    },

    methods:{

        back() {
            window.location.href = "uniwebview://mofangvr.com?action=back";
        },
        jumpDetail: function(msg){

            EventBus.$emit("getId",msg);
            this.$router.push({
                path: `/newsDetail/${msg}`,
            })
        },
        getNewsBanner(){
            news.getNewsBanner().then(res=>{
                let rs = res;
                this.banner = rs;
            });
        },
        getNewsList(){
            news.getNewsList().then(res=> {
                let rs = res;
                this.array = rs;                
            });
        }
    },
    activated(){
        this.getNewsBanner();
        this.getNewsList();    
    }
}



