function request(url,type,data,suc,errA,option){
    $.ajax({
         type : type,
         url :  url,
         data : data,
         headers : option && option.headers || null,
         dataType : "json",
         beforeSend : option && option.beforeSend,
         success : function(res){
               suc(res);
         },
         error : function(err){
               if(err.status >= 400 &&  err.status < 500){
                    alert(JSON.parse(err.response).message);
               }else if(err.status >= 500){
               }
               errA && errA(err);
               console.log("接口请求有误!");
        }
  })
}

export default request


