export function formatDate (timestamp) {
  let urlTimestamp = timestamp;
  let _time;
  let now;

  now = Date.parse(new Date());

  _time = (now - urlTimestamp) / 1000;

  // 通过变量判断
  if (_time / 60 < 1) {
    return '刚刚';
  } else if (_time / 60 >= 1 && _time / 60 < 60) {
    return parseInt(_time / 60) + '分钟前';
  } else if (_time / 3600 >= 1 && _time / 3600 < 24) {
    return parseInt(_time / 3600) + '小时前';
  } else if (_time / 86400 >= 1 && _time / 86400 < 30) {
    return parseInt(_time / 86400) + '天前';
  } else if (_time / 2592000 >= 1 && _time / 2592000 < 12) {
    return parseInt(_time / 2592000) + '月前';
  } else {
    return parseInt(_time / 31104000) + '年前';
  }
}



