import http from '../assets/js/http.js'
import cookie from 'vux/src/tools/cookie/index.js'

let status;

let userInfo = null;
export default {
getStatus: () => {
return status
},
/**
* 用户登录
*/
login: (tel, password) => {
return http.post('user/login', {
tel: tel,
password: password
}).then((data) => {
status = 1;
localStorage.setItem("user-token",data.result["user-token"]);

return Promise.resolve();
});
},
/**
* 用户登录和注册
*/
login_register: (tel, code) => {
return http.post('/user/login_register', {
tel: tel,
code: code
}).then((data) => {
status = 1;
localStorage.setItem("user-token",data.result["user-token"]);
return Promise.resolve();
});
},
/**
* 用户登出
*/
loginOut: () => {
return http.get('user/logout', {}).then(() => {
status = 0;
localStorage.removeItem('user-token');
userInfo = null;
return Promise.resolve();
});
},
/**
* 获取用户验证码
*/
getVerificationCode: (tel) => {
return http.post('user/verification_code', {
tel: tel
});
},
/**
* 用户注册
*/
register: (tel, name, password, code) => {
return http.post('user/register', {
tel: tel,
name: name,
password: password,
code: code
});
},
/**
* 用户修改密码
*/
changePassword: (tel, password, code) => {
return http.post('user/changePassword', {
tel: tel,
password: password,
code: code
});
},

/**
* 获取用户信息
*/
getInfo: () => {
return http.post('user/getInfo', {
}).then((res) => {
userInfo = res.result;
return Promise.resolve(res);
});
},
/**
* 返回用户信息
*/
returnInfo: () => {
return userInfo;
},
/**
* 获取用户余额
*/
getBalance: (_coin) => {
let resData = {};
if(_coin){
resData = {coin: _coin}
}
return http.post('user/balance', resData).then((res) => {
let coin = 0;
if (res.result.list[0]) {

coin = res.result.list[0].balance;
coin = parseFloat(coin).toFixed(2);
}
return Promise.resolve(coin);
});
},

/**
*
* 获取账户余额列表
*/
getBalanceList: (_coin) => {
let resData = {};
if(_coin){
resData = {coin: _coin}
}
return http.get('user/balance', resData).then((res) => {
return Promise.resolve(res.result.list)
})
}

}



