import headerView from "./../../components/headerBack";
import news from "./../../model/news.js";
import eventBus from './../../assets/js/eventBus.js';
import {formatDate} from '../../assets/Tools/filtersTime.js';
import CryptoJS from 'crypto-js'

export default {
    components:{
        headerView
    },
    data(){
        return{
            title:  this.$t("message.newsDetail"),
            id: 1,
            newsdata:{
                title:null,
                author:null,
                updateTime:null,
                hotCounts:null,
                synopsis:null,
                simditor:null
            },     
        }
    },
    filters: {
        formatDate(time) {
            return formatDate(time);
        }
    },
    methods:{

        getDetailId: function() { // 获取页面ID
            eventBus.$on("getId", message => {
                this.id = message;
            })
        },
        getNewsDetail(){    // 获取页面信息
            news.getNewsDetail(this.$route.params.id).then(res=> {
                let news = res[0];
                this.newsdata.title = news.title;
                this.newsdata.author = news.author;
                this.newsdata.updateTime = news.updateTime;
                this.newsdata.hotCounts = news.hotCounts;
                this.newsdata.synopsis = news.synopsis;
                
                // news base64 to utf-8
                let simditor = CryptoJS.enc.Base64.parse(news.simditor).toString(CryptoJS.enc.Utf8);
                simditor.replace("&lt;","<");
                simditor.replace("&gt;",">");
                
                this.newsdata.simditor = simditor;
                this.$refs.context.innerHTML = this.newsdata.simditor;

            });
        },
    },
    activated(){
        this.getDetailId();
        this.getNewsDetail();
    },
    deactivated(){
        this.newsdata = {
                title:null,
                author:null,
                updateTime:null,
                hotCounts:null,
                synopsis:null,
                simditor:null
            }
    }
    
}



