import EventBus from './../../assets/js/eventBus.js';
import {formatDate} from '../../assets/Tools/filtersTime.js'

export default {
   
    data(){
        return{

        }
    },
    props: {
        date: {
            type: Array,
            default: []
        }
    },
    filters: {
        formatDate(time) {
            return formatDate(time);
        }
    },
    methods:{
        
        jumpDetail: function(msg){

            EventBus.$emit("getId",msg);
            this.$router.push({
                path: `/newsDetail/${msg}`,
            })
        },
    }   
}




