import Vue from 'vue';
import Router from 'vue-router';
import List from '../components/list.vue';
import Mall from '../components/mall.vue';
// import Myself from '../components/myself.vue';
import Myself from '../view/user/myself.vue';
import Login from '../view/user/login.vue';
import Detail from '../components/detail';

import Register from '../view/user/register.vue';

import Authorization from '../view/user/authorization';

import MySetting from '../view/user/mySetting.vue';

import ChangePassword from '../view/user/changePassword.vue';

import newsList from '../view/news/index.vue';

import newsDetail from '../view/news/newsDetail.vue';

import LoginRegister from '../view/user/login_register.vue';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/app/list',
      component: List
    },
    {
      path: '/detail/:coin',
      component: Detail
    },
    {
      path: '/app/news',
      component: newsList
    },
    {
      path: '/newsDetail/:id',
      component: newsDetail
    },
    {
      path: '/app/mall',
      component: Mall
    },
    {
      path: '/app/myself',
      component: Myself
    },
    {
      path: '/mine/setting',
      component: MySetting
    },
    {
      path: '/',
      redirect: '/app/news'
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/authorization',
      component: Authorization
    },
    {
      path: '/register',
      component: Register
    },
    {
      path: '/changePassword',
      component: ChangePassword
    }
  ]
});



