import util from "../../assets/Tools/util.js";
import Api from "../../assets/Tools/Api.js";
import user from "../../model/user";

import headerBack from "../../components/headerBack";

export default {
  components: {
    headerBack
  },
  data() {
    return {
      title: "修改密码", //
      codeText: "获取验证码",
      telephone: "",
      password: "",
      registerCode: null,
      newTelephone: "",
      username: "",
      newPassword: "",
      code: "",
    };
  },
  methods: {
    //修改密码
    check() {
      let telReg = /^(((13[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(16[0-9]{1})|(19[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
      let passwordReg = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{6,14}$/;

      if (this.telephone == "" || !telReg.test(this.telephone)) {
        this.$vux.toast.text("手机号错误");
        return;
      }
  
      if (this.password == "" || !passwordReg.test(this.password)) {
        this.$vux.toast.text("密码要求包含大小写数字,至少6位以上");
        return;
      }
      this.$vux.loading.show({
        text: "Loading"
      });
      // 用户开始注册流程
      user
        .changePassword(this.telephone,this.password, this.code)
        .then(() => {
          this.$vux.loading.hide();
          this.$vux.toast.show({
            text: "修改成功"
          });
          // 注册成功后跳转登录
          this.$router.push("/login");
        });
    },
    //获取验证码
    getCode() {
      let telReg = /^(((13[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(16[0-9]{1})|(19[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
      if (this.telephone == "" || !telReg.test(this.telephone)) {
        this.$vux.toast.text("请输入正确的手机号码");
        return;
      }
      this.$vux.toast.text("验证码已发送");
      user.getVerificationCode(this.telephone).then(() => {
        console.log("验证码获取成功");
      });
    }
  },
  mounted() {},
  created() {}
};



