import util from "./../../assets/Tools/util.js";
import Api from "../../assets/Tools/Api.js";
import request from "../../assets/js/common.js";
import cookie from "common-cookie";

import user from "../../model/user.js";
import market from "../../model/market.js";
import MarketBalanceFactory from "../../model/MarketBalanceFactory.js";

import headerView from "../../components/headerBack";

export default {
  components: {
    headerView
  },
  data() {
    return {
      title: "我的",
      tapIndex: 2,
      balances: [],
      account: {
        avatarImg:
          "http://oojfnrmgt.bkt.clouddn.com/ec6715e7aca979867af1da01bcafca06",
        id: "0",
        name: "点击登录", //$t("message.myselfDefaultName")
        exp: "0"
      },
      order:[],
      deal:[],
      tapItemIndex: 0,
      setingView: false,
      isLogin: false,
      loginview: false,
      selectLanguage: 0
    };
  },
  methods: {
    //切换tap
    selectTap(index) {
      this.tapItemIndex = index;
    },
    //显示设置
    showLogin() {
    
      if (localStorage.getItem('user-token')) {
        this.$router.push("/mine/setting");
      } else {
        this.$router.push("/login");
      }
    },
    //关闭设置
    closeSeting() {
      this.setingView = false;
    },

    getCouponSelected() {
      console.log(this.selectLanguage);
      let locale = this.$i18n.locale;
      locale === "zh-CN"
        ? (this.$i18n.locale = "en")
        : (this.$i18n.locale = "zh-CN");
    }
  },
  mounted() {
    this.selectLanguage = "zh-CN";
    user.getInfo().then(res => {
      let marketTypeList = [];
      var result = res.result;
      this.account.exp = result.exp;
      this.account.name = result.name;
      this.account.id = result.user_id;

      Promise.all([user.getBalanceList(), market.getAllLatestPrices()]).then(
        (data) => {
          let balances = data[0];
          let markets = data[1];
          let marketBalanceFactory = new MarketBalanceFactory(markets);

          balances.forEach(element => {
            let balance = {
              market: element.currency,
              balance: parseInt(element.balance).toFixed(2),
              price:marketBalanceFactory.getBalance(element.currency + "/MCC").price || 0,
              gain:marketBalanceFactory.getBalance(element.currency + "/MCC").changePercentage || 0,
            };

            this.balances.push(balance);
          });
        }
      );

    });
  }
};



