import http from '../assets/js/http.js'

var userAccessToken = {
  token: null,
  expire_time: '',
};


let module = {

  /* 获取币种行情详情 (页面头部信息) */
  getLatestMarketDetail: (coin) => {
    return http.thirdPartyGet('api/tick/getLatestMarketDetail', { market: coin })
      .then((res) => {
        return Promise.resolve(res.Result);
      });
  },

  // 获取玩家在交易所的财产信息
  getUserPropertyDetail: (pramas) => {
    return http.get('https://test.mofangvr.com/exchange/exchange/accountToTrade', null)

      .then((res) => {
        let uids = res.data.list;

        uids.map(item => {
          pramas.Accounts.push(item.uid.toString());
        });

        pramas = JSON.stringify(pramas);

        return http.thirdPartyPost(url + '/api/third/GetBalances', pramas).then((res) => { return Promise.resolve(res.Result); });
      });
  },

  /*
    获取第三方TOKEN
  */

  getEndUserAccountToken: () => {
    if (userAccessToken.token && new Date().getTime() < userAccessToken.expire_time + 54000000) {
      return Promise.resolve(userAccessToken.token);
    } else {
      return http.post('user/getEndUserAccessToken', null)
        .then((res) => {
          userAccessToken.token = res.result.token;
          userAccessToken.expire_time = new Date().getTime();
          // console.log("currenttime",userAccessToken.expire_time,"userAccessToekn", userAccessToken.token)
          return Promise.resolve(userAccessToken.token)

        })
    }
  },

  /* 获取币种未撮合的订单（委托订单） */
  getOpenOrdersDetail: (coin) => {

    return module.getEndUserAccountToken().then(token => {
      var headOparetion = {
        'Content-Type': 'application/x-www-form-urlencoded',
        'Authorization': "Bearer " + token
      }
      return http.thirdPartyGet('api/Market/GetOpenOrders', { market: coin }, headOparetion)
        .then((res) => {
          return Promise.resolve(res.result);
        })
    });
  },

  /**
   * 在限定的交易对里下达限定买单
   * market： 类型
   * price： 价格
   * quantity: 数量
   */

  BuyLimit: (BuyLimParmas) => {

    return module.getEndUserAccountToken().then(token => {
      var headOparetions = {
        'Content-Type': 'application/x-www-form-urlencoded',
        'Authorization': "Bearer " + token
      }
      return http.thirdPartyGet('api/Market/BuyLimit', BuyLimParmas, headOparetions)
        .then((res) => {
          return Promise.resolve(res)
        })
    })
  },

  /**
  * 在限定的交易对里下达限定卖单
  * market： 类型
  * price： 价格
  * quantity: 数量
  */

  SellLimit: (SellLimParmas) => {

    return module.getEndUserAccountToken().then(token => {
      var headOparetion = {
        'Content-Type': 'application/x-www-form-urlencoded',
        'Authorization': "Bearer " + token
      }
      return http.thirdPartyGet('api/Market/SellLimit', SellLimParmas, headOparetion)
        .then((res) => {
          return Promise.resolve(res)
        })
    })
  },


  /**
   * 用户获取我的订单
  */
  userOrderList: (coinsType) => {
    return http.post('order/list',{market: coinsType})
      .then((res) => {
        return Promise.resolve(res.result.list)
      })
  }
}

export default module



