import user from "../../model/user";

import headerBack from "../../components/headerBack";

import RegExpUtil from "../../util/RegExpUtil"

import yzmView from "../../components/yzm"

export default {
  components: {
 
    headerBack,
    yzmView
  },
  data() {
    return {
      title: this.$t("register.title"), //
      codeText: "获取验证码",
      telephone: "",
      password: "",
      registerCode: null,
      newTelephone: "",
      username: "",
      newPassword: "",
      code: "",
    };
  },
  methods: {
   
    //修改密码
    check() {

      if (!RegExpUtil.isMobile(this.telephone)) {
        this.$vux.toast.text("请输入正确的手机号码");
        return;
      }
      if (this.username == "") {
        this.$vux.toast.text("请输入正确的用户名");
        return;
      }
      if (!RegExpUtil.isPassword(this.password)) {
        this.$vux.toast.text("请输入密码");
        return;
      }
      this.$vux.loading.show({
        text: "Loading"
      });
      // 用户开始注册流程
      user
        .register(this.telephone, this.username, this.password, this.code)
        .then(() => {
          this.$vux.loading.hide();
          this.$vux.toast.show({
            text: "注册成功"
          });
          // 注册成功后跳转登录
          this.$router.push("/login");
        });
    },
    //获取验证码
    getCode() {
        if (!RegExpUtil.isMobile(this.telephone)) {
        this.$vux.toast.text("请输入正确的手机号码");
        return;
      }
      this.$vux.toast.text("验证码已发送");
      user.getVerificationCode(this.telephone).then(() => {
        console.log("验证码获取成功");
      });
      return true;
    }
  },
  mounted() {},
  created() {}
};



