export default {

    data() {

        return {

            message: 0,

            type: 0,
            display:true

        }



    },

    watch: {
        '$route' (to, from) {
            if((/app/).test(to.path)){
                this.display = true;
            } else{
                this.display = false;
            }
        }
        },

        props: [
        ],

        methods: {


        },

        mounted() {
           if((/app/).test(location.href)){
                this.display = true;
            } else{
                this.display = false;
            }

        }

    }



