import http from "../../assets/js/http.js";
import user from "../../model/user";

import auth from "../../model/auth";
import headerBack from "../../components/headerBack";
import yzmView from "../../components/yzm";
import RegExpUtil from "../../util/RegExpUtil"
import URLUtil from '../../util/URLUtil.js'

export default {
  components: {
    headerBack,
    yzmView
  },
  data() {
    return {
      title: "登陆并注册",
      telephone: "",
      password: "",
      code: ""
    };
  },
  methods: {
    //登录
    loginIn() {
      if (!this.telephone) {
        this.$vux.toast.text("手机号不能为空");
        return;
      }
      if (!this.code) {
        this.$vux.toast.text("验证码不能为空");
        return;
      }

      this.$vux.loading.show({
        text: "Loading"
      });

      user
        .login_register(this.telephone, this.code)
        .then(res => {
          this.$vux.loading.hide();

          this.$vux.toast.show({
            text: "登录成功"
          });

          if (auth.isUseAuth()) {
            this.$router.push("/authorization");
          } else {
            this.$router.push("/");
          }
        })
        .catch(res => {
          this.$vux.loading.hide();
          // 显示错误信息
        });
    },
    //获取验证码
    getCode() {
      if (!RegExpUtil.isMobile(this.telephone)) {
        this.$vux.toast.text("请输入正确的手机号码");
        return;
      }
      this.$vux.toast.text("验证码已发送");
      user.getVerificationCode(this.telephone).then(() => {
        console.log("验证码获取成功");
      });
      return true;
    }
  },
  mounted() {
    
  },
  created() {
    
  },
  activated() {
    console.log(URLUtil.getValue("autotel"))
    this.telephone = URLUtil.getValue("autotel");
  }
};



