import util from "./../assets/Tools/util.js";
import Api from "../assets/Tools/Api.js";
import request from "../assets/js/common.js";
import cookie from "common-cookie";
import Detail from "./detail";

import market from "../model/market.js";
import EventBus from '../assets/js/eventBus.js'
import headerView from "../components/headerBack";

const url = "http://47.96.91.195:8085";

export default {
  components: {
    Detail,
    headerView
  },
  data() {
    return {
      coins: [],
      tapIndex: 0,
      message: 0,
      parmas: "",
      marketDetailView: false,
      marketView: true,
      coinsType: "",
      testDate: [
       
      ]
    };
  },
  methods: {
    // to(type) {
    //   //    let params = util.(window.location.search);
    //   //    location.href = "./trparseUrlade.html?token=" +  params.token + '&type=' + this.type
    //   this.marketDetailView = true;
    //   this.marketView = false;

    //   this.coinsType = type;
    //   console.log(this.coinsType);
    // },
    //返回
    back() {
      window.location.href = "uniwebview://mofangvr.com?action=back";
    },
    testPay() {
      location.href = "https://pro.mofangvr.com/assets/view/test.html";
    },
    //请求list列表
    getList() {

      this.$vux.loading.show({
        text: "Loading"
      });
      // 请求当前的行情
      market.getAllLatestPrices().then(res => {
        this.testDate = res;
        this.$vux.loading.hide();
      });

    },
    //切换tap
    changeTap(index) {},
    detailViewStatus(detailView) {
      console.log(detailView);
      this.marketDetailView = false;
      this.marketView = true;
    },
    /**
     * 
     * 跳转到marketDetail 页面
     */
    jumpDetail: function(msg){

      msg = msg.replace("/","_");
     
      EventBus.$emit("getMarketType",msg);
        this.$router.push({
          path: `/detail/${msg}`,
        })
    }


  },
  mounted() {
    this.getList();
  },
  created() {}
};



