import { Toast} from 'mint-ui';

class Modal{
    constructor(){}
    /**
     * 显示模态提示信息
     * @param {String} message 
     */
    showToast(message){
        Toast({
            message : message,
            duration : 1000,  
        })
    }
}

export default  new Modal()


