import util from "./../../assets/Tools/util.js";
import Api from "../../assets/Tools/Api.js";
import request from "../../assets/js/common.js";
import cookie from "common-cookie";

import auth from "../../model/auth";
import user from "../../model/user";
import exchange from "../../model/exchange";

import URLUtil from "../../util/URLUtil";

export default {
  components: {},
  data() {
    return {
      authorizeText: "获取成功",
      authorizeFail: "https://cdn.mofangvr.com/h5/img/fail.png",
      authorizeSuccess: "https://cdn.mofangvr.com/h5/img/success.png",
      authenticateName: "应用名称",
      authenticateparty: "https://cdn.mofangvr.com/h5/img/four_ leaf_grass.png"
    };
  },
  methods: {
    //第三方用户授权
    authorize() {
      this.$vux.loading.show({
        text: "Loading"
      });

      auth
        .authorize()
        .then(res => {
          this.$vux.loading.hide();
          this.$vux.toast.show({
            text: "授权成功"
          });
          console.log("成功", res);
          auth.authNotice(res);
        })
        .catch(res => {
          this.$vux.toast.show({
            text: "授权失败"
          });
          console.log("失败", res);
          auth.authNotice(res);
        });
    },

    //返回键
    back() {
      window.location.href = "uniwebview://mofangvr.com?action=back";
    },
  },
  activated() {
    let loginState = user.getStatus();
    // 如果没有登录,就切换到登录界面
    if(!loginState){
        this.$router.push("/login");
        return ;
    }
    // 获取应用信息
    let access_token = URLUtil.getValue("access_token");
    this.$vux.loading.show({
      text: "Loading"
    });

    exchange.getInfo(access_token).then(res => {
      let info = res.result.info;
      this.$vux.loading.hide();
      this.authenticateName = info.name;
      this.authenticateparty = info.icon;
    })
  },
  created() {}
};



