import http from "../../assets/js/http.js";
import user from "../../model/user";

import auth from "../../model/auth";
import headerBack from "../../components/headerBack";

export default {
  components: {
    headerBack
  },
  data() {
    return {
      title: "Login", //
      telephone: "",
      password: ""
    };
  },
  methods: {
    //登录
    loginIn() {
      if (!this.telephone) {
        this.$vux.toast.text("手机号不能为空");
        return;
      }
      if (!this.password) {
        this.$vux.toast.text("密码不能为空");
        return;
      }

      this.$vux.loading.show({
        text: "Loading"
      });

      user
        .login(this.telephone, this.password)
        .then(res => {
          this.$vux.loading.hide();

          this.$vux.toast.show({
            text: "登录成功"
          });

          if (auth.isUseAuth()) {
            this.$router.push("/authorization");
          } else {
            this.$router.push("/");
          }
        })
        .catch(res => {
          this.$vux.loading.hide();
          // 显示错误信息
        });
    }
  },
  mounted() {},
  created() {}
};



