
import http from '../assets/js/http.js';

export default {

  /* 获取新闻列表 */
  getNewsList: () => {
    return http.get('news/list', {})
      .then((res) => {
        return Promise.resolve(res.result);
      });
  },

  /* 获取对应ID新闻内容 */
  getNewsDetail: (detailID) => {
    return http.get('news/detail', { id: detailID })
      .then((res) => {
        return Promise.resolve(res.result);
      });
  },

  getNewsBanner: () =>{
    return http.get('news/recommend')
      .then( (res) => {
        return Promise.resolve(res.result);
      });
  }
}



