//
//

var c = 60;
var second = 60;
var t;
var clickFn = new Function();
var completeFn = new Function();
var sText = "获取验证码";

export default {
    props: {
    clickFn: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      text: sText
    };
  },
  methods: {
    timedCount() {
      this.text = "重新获取" + c + "s";
      c = c - 1;
      if (c == -1) {
        this.complete();
        return;
      }
      t = setTimeout(this.timedCount, 1000);
    },
    start() {
      if (c == second) {
        if(this.clickFn())
        this.timedCount();
      }
    },
    complete() {
      c = second;
      this.text = sText;
      completeFn();
    }
  }
};



